#ARTinfo{
  color:rgb(231, 129,0);
  font-family: 'Anderson Grotesk';
  font-size: 15px; 
  margin-bottom:2px;
}

#ARTname{
    color:rgb(231, 129,0);
    font-size: 18px;
    width: 250px;
    margin-bottom: 8px;    
}

#ARTcode{
    color:rgb(231, 129,0);
    font-size: 25px;
    width: 250px;
    font-family: 'Anderson Grotesk';
    padding-top: 10px;
    margin-bottom:-5px;
}

#ARTbox{
    background-color:white;
    width:270px;
    padding-left:13px;
    border: 1.5px solid rgb(231, 129,0);
    border-radius:5px;
}

